@import './reset.scss'; //2. import가능

$mainColor: #ff0000; //1. 변수를 만들어 저장해둘 수 있다.

.red {
 color: $mainColor;
}

// 5. @mixin으로 함수?오브젝트? 같은 것을 만들 수 있다.
@mixin 함수() {
    background: #eeeeee;
    padding: 15px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.my-alert {
    @include 함수() // mixin가져다 쓰러면 include 그리고 mixin이 위에 있어야함
}

.my-alert2 {
    @extend .my-alert;  //4. extend로 기존것을 확장할 수 있음
    background: #ffe591; //색깔만 바꿈
}

.my-alert { 
    p {
        margin-bottom: 0;
    }
}

